import React, { Component } from 'react';
import { AuthContext } from '../hooks/KeycloakContext'
// TODO: make this component in a functional component
class App extends Component {
    static contextType = AuthContext;

    domain = 'meet.jit.si/digital-workplace';
    api = {};

    constructor(props) {
        super(props);
        this.state = {
            room: '',
            roomPassword: '',
            isAudioMuted: false,
            isVideoMuted: false,
            listenersInitiated: false,
            redirectUrl: ''
        }
    }

    startMeet = () => {
        let { user } = this.context;

        const options = {
            roomName: this.state.room,
            width: '100%',
            height: 500,
            parentNode: document.querySelector('#jitsi-container'),
            userInfo: {
                displayName: user.preferredName,
                password: 'abcd'
            },
            enableClosePage: false,
            enableWelcomePage: false,

        }
        this.api = new window.JitsiMeetExternalAPI(this.domain, options);

        this.api.addEventListeners({
            readyToClose: this.handleClose,
            participantLeft: this.handleParticipantLeft,
            participantJoined: this.handleParticipantJoined,
            videoConferenceJoined: this.handleVideoConferenceJoined,
            videoConferenceLeft: this.handleVideoConferenceLeft,
            audioMuteStatusChanged: this.handleMuteStatus,
            videoMuteStatusChanged: this.handleVideoStatus,
            participantRoleChanged: this.handleRoleChanged
        });
        this.api.on('passwordRequired', this.handlePasswordRequired);
    }
    handlePasswordRequired = () => {
        const { roomPassword } = this.state;
        this.api.executeCommand('password', roomPassword);
    }
    handleRoleChanged = (event) => {
        const { roomPassword } = this.state;
        if (event.role === "moderator") {
            this.api.executeCommand('password', roomPassword);
        }
    }
    handleClose = () => {
        console.log("handleClose");
    }

    handleParticipantLeft = async (participant) => {
        console.log("handleParticipantLeft", participant); // { id: "12345" }
        const data = await this.getParticipants();
    }

    handleParticipantJoined = async (participant) => {
        console.log("handleParticipantJoined", participant); // { id: "12345", displayName: "Bavithra", formattedDisplayName: "Bavithra" }
        const data = await this.getParticipants();
    }

    handleVideoConferenceJoined = async (participant) => {
        console.log("handleVideoConferenceJoined", participant); // { roomName: "12345", id: "8c35a951", displayName: "Bavithra", formattedDisplayName: "Bavithra (me)"}
        const data = await this.getParticipants();
    }

    handleVideoConferenceLeft = () => {
        console.log("handleVideoConferenceLeft");
        const { redirectUrl } = this.state;
        // return this.props.history.push('/thank-you');
        window.location.replace(redirectUrl);
    }

    handleMuteStatus = (audio) => {
        console.log("handleMuteStatus", audio); // { muted: true }
    }

    handleVideoStatus = (video) => {
        console.log("handleVideoStatus", video); // { muted: true }
    }

    getParticipants() {
        return new Promise((resolve, reject) => {
            setTimeout(() => {
                resolve(this.api.getParticipantsInfo()); // get all participants
            }, 500)
        });
    }

    // custom events
    executeCommand(command) {
        this.api.executeCommand(command);
        if (command == 'hangup') {
            //  return this.props.history.push('/thank-you');
        }

        if (command == 'toggleAudio') {
            this.setState({ isAudioMuted: !this.state.isAudioMuted });
        }

        if (command == 'toggleVideo') {
            this.setState({ isVideoMuted: !this.state.isVideoMuted });
        }
    }

    componentDidMount() {
        const urlParams = new URLSearchParams(window.location.search)
        const redirectUrl = urlParams.get("redirectUrl");
        const room = urlParams.get("r");
        const roomPassword = urlParams.get("p");
        this.setState({
            redirectUrl,
            room,
            roomPassword
        }, () => { console.log(this.state) });

    }

    componentDidUpdate() {
        let auth = this.context;
        const { listenersInitiated } = this.state;

        if (auth && auth.initialized && auth.authenticated && !listenersInitiated) {
            if (window.JitsiMeetExternalAPI) {
                this.startMeet();
            } else {
                alert('JitsiMeetExternalAPI not loaded');
            }
            return this.setState({ listenersInitiated: true });
        }
    }

    render() {
        const { isAudioMuted, isVideoMuted } = this.state;
        return (
            <div className="card video-widget">
                <div id="jitsi-container"></div>
                <div className="item-center">
                    {/*<span>Custom controls if any</span> */}
                </div>
                <div className="item-center">
                    <span>&nbsp;&nbsp;</span>
                    <i onClick={() => this.executeCommand('toggleAudio')} className={`fas fa-2x grey-color ${isAudioMuted ? 'fa-microphone-slash' : 'fa-microphone'}`} aria-hidden="true" title="Mute / Unmute"></i>
                    <i onClick={() => this.executeCommand('hangup')} className="fas fa-phone-slash fa-2x red-color" aria-hidden="true" title="Leave"></i>
                    <i onClick={() => this.executeCommand('toggleVideo')} className={`fas fa-2x grey-color ${isVideoMuted ? 'fa-video-slash' : 'fa-video'}`} aria-hidden="true" title="Start / Stop camera"></i>
                    <i onClick={() => this.executeCommand('toggleShareScreen')} className="fas fa-film fa-2x grey-color" aria-hidden="true" title="Share your screen"></i>
                </div>
            </div>
        );
    }
}

export default App;