import React from 'react';
import CustomElement from './CustomElement';
import Video from '../components/Video';

const ATTRIBUTES = {
  locale: "locale",
  domainLink: "domain-link"
};

class VideoConferenceWidgetElement extends CustomElement {
  static TAG = 'video-conferencing-widget'; // kebab-case

  constructor() {
    super(ATTRIBUTES, VideoConferenceWidgetElement);
  }

  get children() {
    const attrs = this.getAllAttributes();
    return <Video {...attrs} />
  }

  static get observedAttributes() {
    return Object.values(ATTRIBUTES);
  }
}


if (!customElements.get(VideoConferenceWidgetElement.TAG)) {
  customElements.define(VideoConferenceWidgetElement.TAG, VideoConferenceWidgetElement);
}